import { Component, OnInit } from '@angular/core';
import { UserService } from '../user.service';

@Component({
  selector: 'app-specialists-page',
  templateUrl: './specialists-page.component.html',
  styleUrls: ['./specialists-page.component.scss']
})
export class SpecialistsPageComponent implements OnInit {

  emailSent: boolean;

  constructor(
    private _uS: UserService
  ) { }

  ngOnInit() {
  }

  sendEmail(email) {
    if (email.value.split('@').length < 2) {
      alert('Incorrect email');
      return;
    }
    const body = `{\n\t\"EmailAddress\":\"${email.value}\",\n\t\"Source\":\"Specialist\"\n}`;
    this._uS.signUps(body).subscribe(res => {
      this.emailSent = true;
    },
    error => {
      console.log(error);
    });
  }

}
