import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { MainPageComponent } from './main-page/main-page.component';
import { SpecialistsPageComponent } from './specialists-page/specialists-page.component';
import { ForAllPageComponent } from './for-all-page/for-all-page.component';

const routes: Routes = [
  { path: '', component: MainPageComponent },
  { path: 'specialists', component: SpecialistsPageComponent },
  { path: 'for-all', component: ForAllPageComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
