import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class UserService {

  private url = 'http://andrewmeli-001-site2.atempurl.com';
  private _options = { headers: new HttpHeaders({ 'Content-Type': 'application/json', 'Access-Control-Allow-Origin': '*' }) };

  constructor(
    private http: HttpClient
  ) { }

  signUps(body) {
    return this.http.post(`${this.url}/api/SignUps`, body, this._options);
  }

}
