import { Component, OnInit } from '@angular/core';
import { UserService } from '../user.service';

@Component({
  selector: 'app-for-all-page',
  templateUrl: './for-all-page.component.html',
  styleUrls: ['./for-all-page.component.scss']
})
export class ForAllPageComponent implements OnInit {

  emailSent: boolean;

  constructor(
    private _uS: UserService
  ) { }

  ngOnInit() {
  }

  sendEmail(email) {
    if (email.value.split('@').length < 2) {
      alert('Incorrect email');
      return;
    }

    
    const body = `{\n\t\"EmailAddress\":\"${email.value}\",\n\t\"Source\":\"Client\"\n}`;
    this._uS.signUps(body).subscribe(res => {
      this.emailSent = true;
    },
    error => {
      console.log(error);
    });
  }
}
